// export default App;
import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'

import Home from './taobao/Home'
import Shoppingcar from './taobao/Shoppingcar'
import My from './taobao/My'
import Goods from './taobao/Goods'
import Search from './taobao/Search'
import Fgoods from './taobao/Fgoods'
import Sgoods from './taobao/Sgoods'
import Tgoods from './taobao/Tgoods'
import Fogoods from './taobao/Fogoods'
import Figoods from './taobao/Figoods'
import Sigoods from './taobao/Sigoods'
import Segoods from './taobao/Segoods'
import Egoods from './taobao/Egoods'
import Ngoods from './taobao/Ngoods'
import Tegoods from './taobao/Tegoods'

const App = ()=>{
	return <Router>
		<Switch>
			<Route  exact path='/react2/build' component={Home} ></Route>
			<Route  path='/home' component={Home} ></Route>
			<Route  path='/goods' component={Goods} ></Route>
			<Route  path='/home/search' component={Search} />	
			<Route  path='/fgoods' component={Fgoods} />
			<Route  path='/sgoods' component={Sgoods} />
			<Route  path='/tgoods' component={Tgoods} />
			<Route  path='/fogoods' component={Fogoods} />
			<Route  path='/figoods' component={Figoods} />
			<Route  path='/sigoods' component={Sigoods} />
			<Route  path='/segoods' component={Segoods} />
			<Route  path='/egoods' component={Egoods} />
			<Route  path='/ngoods' component={Ngoods} />
			<Route  path='/Tegoods' component={Tegoods} />
			<Route  path='/shoppingcar' component={Shoppingcar}></Route>
			<Route  path='/my' component={My}></Route>
		</Switch>
	</Router>
}
// export default App;
