import React from 'react'
import Mytap from './Mytap'

const Tgoods = (props) => {
    return (
        <div>
            <img src='https://gw.alicdn.com/bao/uploaded/i2/TB1nGBoFVXXXXa6XVXXXXXXXXXX_!!0-item_pic.jpg_500x500q90.jpg_.webp' style={{width:'375px'}}/>
            <button style={{
                width:'35px',
                height: '35px',
                borderRadius:'10rem',
                border:0,
                position: 'relative',
                top:'-360px',
                left:'10px',
                display:'block',
                background:'rgba(0, 0, 0, 0.4)',
                color: '#fff'}}
                onClick={props.history.goBack}
            > <i className='iconfont icon-shangyiji'  ></i> </button>
             <Mytap />
        </div>
    )
}

export default Tgoods
